:root{
  --bgcolor:url('https://www.xmple.com/wallpaper/white-blue-gradient-linear-1440x2560-c2-ffffff-add8e6-a-255-f-14.svg');
  --bgcolor-primary:#1f1f1f;
  --bgcolor-secendory:rgba(235, 235, 245, 0.03);
  --font-color:white;
  --font-color-light:white;
  --headertext:1.2rem;
  --textmedium:1rem;
  --smalltext:0.5rem;

  --container-width-lg:75%;
  --container-width-md:86%;
  --container-width-sm:95%;
  --transtion:all 400ms ease;
}
body{
