.btn{
  display: inline-block;
    font-weight: 400;
    text-align: center;
    white-space: nowrap;
    vertical-align: middle;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;color: #fff;
    background-color: rgba(235, 235, 245, 0.03);
    background-color: var(--bgcolor-secendory);
    border-color:#1f1f1f;
    border-color:var(--bgcolor-primary);
    user-select: none;
    border: 1px solid transparent;cursor: pointer;
    padding: 0.375rem 0.75rem;
    font-size: 1rem;
    line-height: 1.5;margin: 0px 5px;
    border-radius: 0.25rem;
    transition: all 400ms ease;
    transition: var(--transtion);
   
}
.btn:hover{background-color: #1f1f1f;background-color: var(--bgcolor-primary); color: white;}
