.Tabs {
  width: 100%;
  height: auto;
  min-height: 400px;
  
   
  padding: 2rem 1rem;
  color: #E8F0F2;
  border-radius: 2rem;
}
 
.react-tabs {
  -webkit-tap-highlight-color: transparent;
}
 
.react-tabs__tab-list {
  width: 100%;
  margin: 0 auto 2rem;color: #fff;
  display: flex;
  align-items: center;
  justify-content: space-between;
  border: 1px solid #fff;
  
  border-top-left-radius: 2rem;
  border-bottom-right-radius: 2rem;
  padding-left: 0px;
}
 
.react-tabs__tab {
  width: 50%;
  padding: 1rem;
  list-style: none;
  text-align: center;
  cursor: pointer;
  transition: all 0.7s;
}
 
.react-tabs__tab--selected {
  background: var(--bgcolor-secendory);color: #fff;
}
 
.react-tabs__tab--disabled {
  color: GrayText;
  cursor: default;
}
 
.react-tabs__tab:hover {
  background:var(--bgcolor-secendory);color: #fff;
}
 
.react-tabs__tab:first-child {
   
  border-top-left-radius: 2rem;
}
 
.react-tabs__tab:last-child {
  border-bottom-right-radius: 2rem;
 
}
 
.react-tabs__tab-panel {
  display: none;
}
 
.react-tabs__tab-panel--selected {
  display: block;
  font-size: 2rem;
  text-align: center;
